@import url("https://fonts.googleapis.com/css2?family=Prompt:wght@300;400;500;600;700;900&display=swap");
:root {
  --font-family: "Prompt", sans-serif;
  --content-width: 1200px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --font-l: 300;
  --font-r: 400;
  --font-m: 500;
  --font-sb: 600;
  --font-b: 700;
  --font-bk: 900;
  --h-tr: all 0.5s ease;
  --d-tr: all 0.3s ease;
}

.box-container {
  width: 925px;
  float: right;
  padding: 0 15px;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */
/* stylelint-disable */
html {
  box-sizing: border-box;
}

ul,
p,
h3,
h2,
h1 {
  list-style: none;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  border: none;
}

.btn-reset {
  border: none;
  background: #f2f2f2;
  border-radius: 40px;
  font-size: 12px;
  line-height: 18px;
  font-weight: var(--font-r);
  height: 46px;
  padding: 0px 20px;
  width: 100%;
  cursor: pointer;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  font-weight: var(--font-r);
  font-size: 16px;
  line-height: 20px;
  color: #fff;
  background: #222939;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: #fff;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: var(--container-width);
}

.box-cont {
  width: 925px;
}

.navigation {
  background: rgb(1, 140, 253);
  padding: 20px;
}
.navigation-logo a {
  color: #000;
  font-size: 25px;
  font-weight: bold;
}
.navigation-menu ul li a {
  font-weight: bold;
  text-transform: capitalize;
}
.navigation-menu ul li a:hover {
  color: red;
}

.weather {
  margin: 0 auto;
  margin-top: 50px;
  width: 40%;
  height: 100%;
  border-radius: 30px;
  background: rgb(2, 0, 36);
  background: linear-gradient(8deg, rgb(2, 0, 36) 0%, rgb(6, 101, 161) 69%);
}
.weather-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 40px;
}
.weather-search {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
}
.weather-search input {
  outline: none;
  width: 250px;
  height: 40px;
  border-radius: 25px;
  padding-left: 10px;
}
.weather-search button {
  margin-left: 10px;
  outline: none;
  height: 40px;
  width: 40px;
  background: #fff;
  border-radius: 100%;
}
.weather-error {
  width: 50%;
  display: none;
  text-align: right;
  margin-top: 5px;
}
.weather-error p {
  text-align: right;
  color: red;
}
.weather-inner {
  display: none;
}
.weather-img {
  text-align: center;
  margin-top: 10%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.weather-img img {
  margin: 0 auto;
  max-width: 200%;
}
.weather-img h1 {
  margin-top: 30px;
  font-size: 80px;
  font-weight: bold;
}
.weather-img h2 {
  margin-top: 10px;
  font-size: 70px;
  font-weight: 300;
}
.weather-details {
  margin: 50px 0 80px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}
.weather-details__box {
  display: flex;
  align-items: center;
  justify-content: center;
}
.weather-details__box span {
  font-size: 40px;
}
.weather-hum__num {
  font-size: 25px;
  font-weight: 300;
}
.weather-wind__num {
  font-size: 25px;
  font-weight: 300;
}
.weather-hum h3, .weather-wind h3 {
  font-size: 16px;
}